<template>
  <div class ="sourceselection">
    <div class="jumbotron">

  <h2><span class="glyphicon glyphicon-list-alt"></span> Daily News</h2>
    <h4>&nbsp; &nbsp; &nbsp; Select News Source</h4>
    <meta name="description" content="">
    <meta name="viewport" content="width=device-width, initial-scale=1.0">
    <select class="form-control" v-on:change="sourceChanged">
    <option v-bind:value="source.id" v-for="source in sources">{{source.name}}</option>
    </select>
    <div v-if="source">
      <h6>{{source.description}}</h6>
      <a v-bind:href="source.url" class="btn btn-lg btn-primary btn-block" target="blank">Go To {{source.name}} Website</a>
    </div>
  </div>
  </div>
</template>

<script>
  export default {
    name: 'sourceselection',
    data () {
      return {
        sources: [],
        source: ''
    }
  },
  methods: {
    sourceChanged: function (e) {
      for (var i=0; i<this.sources.length; i++) {
        if (this.sources[i].id == e.target.value) {
          this.source = this.sources[i];

      }
    }
    this.$emit('sourceChanged', e.target.value);
    }
  },

created: function () {
  this.$http.get('https://newsapi.org/v1/sources?language=en')
      .then(response => {
        this.sources = response.data.sources;
      });

  }
}
</script>

<style scoped>
  .jumbotron {
    padding:1em;
    zoom: 175%;
    height: 16em;
    border-top: 1px solid #f8b500;
    border-radius: .5em;
    /*background-color: #3491e4;*/
    font-family: 'Lato', arial, sans-serif;
    background: #F0F2F0;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to bottom, #1576cc, #F0F2F0);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to bottom, #1576cc, #F0F2F0); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

  h2{
    text-shadow: 1px .5px 0 #f8b500;
    color:#163761;
    text-align: center;
    font-weight: bold;
    font-size: 3.5rem;
}
  h4{
    text-shadow: 1px .5px 0 #f8b500;
    color:#163761;
    text-align: center;
    font-size: 2rem;
  }
  h6{
    margin-top: .5em;

    font-weight: 700;
    color:#163761;
    font-size: 1rem;
    padding: .5em 0 .25em 0;

  }
  .btn-primary{
    display:block; /* change this from inline-block */
    width:40%; /* setting the width */
    margin:0 auto; /* this will center  it */
    position:relative;
    font-style:normal;
    font-weight:normal;
    font-family: 'Lato', arial, sans-serif;
    font-size:14px;
    outline:#f8b500;
    color:#fff;
    border:none;
    text-decoration:none;
    text-align:center;
    border-radius:.5em;
    background-color:#163761;
    border:1px solid #f8b500;
  }

  select.form-control {
  color:#163761;
  font-family: 'Montserrat','Lato', arial, sans-serif;
  font-size: 1.5rem;
  height: 2em;
}

  .glyphicon {
    top:4px;
    font-weight: bolder;

}
@media (max-width: 375px){

  .jumbotron  {

     height: 14em;

  }
}
  </style>



